<template>
    <div class="m-reputation-wrapper">
        <div class="back-wrap">
            <el-button @click="goBack">返回列表</el-button>
        </div>
        <div class="m-reputation-content">
            <div class="info-wrapper">
                <div class="title">
                    {{ reputation.szName }}
                    <span>ID:{{ reputation.dwForceID }}</span>
                </div>
                <div class="info-content">
                    <div class="img-wrap">
                        <img
                            v-if="reputation.servant && reputation.servant.szImagePath"
                            :src="getIcon(reputation.servant.szImagePath, 'partner')"
                        />
                        <!-- 默认图片 -->
                        <img v-else class="default-img" src="@/assets/img/reputation/sw-null.jpg" />
                    </div>
                    <div class="switch-wrap">
                        <div class="switch-btns">
                            <div class="item" :class="activeTab === 0 && 'active'" @click="activeTab = 0">声望</div>
                            <div class="item" :class="activeTab === 1 && 'active'" @click="activeTab = 1">知交</div>
                        </div>
                        <div class="content-detail">
                            <div v-show="activeTab === 0" class="detail-wrap">
                                <div class="sub-title">{{ reputation.szName }}</div>
                                <div class="u-desc" v-html="reputation.szDesc"></div>
                                <template v-if="reputation.szMapNames && reputation.szMapNames.length">
                                    <div class="sub-title2">
                                        <svg width="12" height="14" viewBox="0 0 12 14" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:width="'15'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M10.506 13H7.42c.52-.465 1.01-.961 1.469-1.488 1.712-1.968 2.618-4.05 2.618-6.012a5.5 5.5 0 10-11 0c0 1.963.907 4.044 2.625 6.012.457.528.947 1.025 1.47 1.488H1.505a.5.5 0 000 1h9a.5.5 0 000-1zm-4.5-9.5a2 2 0 110 4 2 2 0 010-4z" fill="#000"/></svg>
                                        声望地图
                                    </div>
                                    <div class="u-desc">{{ reputation.szMapNames[0] }}</div>
                                </template>
                                <template v-if="reputation.GroupName">
                                    <div class="sub-title2">
                                        <svg width="16" height="14" viewBox="0 0 16 14" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:width="'15'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M8.023.815h6.169A1.808 1.808 0 0116 2.623v7.02a1.809 1.809 0 01-1.808 1.808H5.365L8.023.815zM6.903 2.55L4.51 12.118h3.732l-.266 1.067H1.808A1.808 1.808 0 010 11.377v-7.02A1.808 1.808 0 011.808 2.55h5.095z" fill="#000"/></svg>
                                        势力类型
                                    </div>
                                    <div class="u-desc">{{ reputation.GroupName }}</div>
                                </template>
                                <template>
                                    <div class="sub-title2">
                                        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:width="'15'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M22 12a10 10 0 11-20 0 10 10 0 0120 0zM7.4 15.4l4.6-4.6 4.6 4.6L18 14l-6-6-6 6 1.4 1.4z" fill="#000"/></svg>
                                        遗失的尊敬
                                    </div>
                                    <div class="u-desc">
                                        {{ getPath(reputation.szName) || "无法使用遗失的尊敬来提高该声望等级进度" }}
                                    </div>
                                </template>
                            </div>
                            <div v-if="reputation.servant" v-show="activeTab === 1" class="detail-wrap">
                                <div class="sub-title">{{ reputation.servant.szNpcName }}</div>
                                <div class="u-desc" v-html="reputation.servant.szDescBrief"></div>
                                <div
                                    class="u-desc"
                                    v-html="reputation.servant.szDescPersonality.replace(/\\n/g, '<br>')"
                                ></div>
                                <template>
                                    <div class="sub-title2">
                                        <svg width="14" height="14" viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:width="'15'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M7 10.333c.922 0 1.708-.322 2.359-.966.65-.645.975-1.434.974-2.367 0-.089-.003-.178-.008-.267a1.538 1.538 0 00-.042-.266 2.282 2.282 0 01-.816 1.292c-.423.339-.917.508-1.484.508-.666 0-1.227-.228-1.683-.684-.456-.455-.683-1.016-.683-1.683 0-.511.144-.97.433-1.375a2.314 2.314 0 011.133-.858H7c-.933 0-1.722.325-2.367.975A3.227 3.227 0 003.667 7c0 .933.322 1.722.966 2.367.645.644 1.434.966 2.367.966zm.683-3.666l.984-.717.966.717-.383-1.134.983-.716-1.2.016-.366-1.166L8.3 4.833l-1.217-.016.984.716-.384 1.134zm-2.916 5.666H3c-.367 0-.68-.13-.942-.392A1.281 1.281 0 011.667 11V9.233L.4 7.95A1.263 1.263 0 010 7c0-.378.133-.694.4-.95l1.267-1.283V3c0-.367.13-.68.392-.942.261-.261.575-.392.941-.391h1.767L6.05.4C6.306.133 6.622 0 7 0s.694.133.95.4l1.283 1.267H11c.367 0 .68.13.942.392.261.261.392.575.391.941v1.767L13.6 6.05c.267.256.4.572.4.95s-.133.694-.4.95l-1.267 1.283V11c0 .367-.13.68-.392.942a1.281 1.281 0 01-.941.391H9.233L7.95 13.6c-.256.267-.572.4-.95.4s-.694-.133-.95-.4l-1.283-1.267z" fill="#000"/></svg>
                                        知交祝福
                                    </div>
                                    <div class="u-desc">
                                        <span>{{ reputation.servant.szBuffName }}</span>
                                        {{ reputation.servant.szBuffDesc }}
                                    </div>
                                </template>
                            </div>
                            <div class="detail-wrap" v-if="activeTab === 1 && !reputation.servant">
                                <div class="u-desc">无</div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="current-progress">
                    <div class="progress-num"><span>当前进度：中立</span><span>0</span></div>
                    <div class="progress-wrap">
                        <div class="progress-value"></div>
                    </div>
                </div>
            </div>
            <div class="map-wrapper" v-if="reputation.szMapNames && reputation.szMapNames.length">
                <div class="title">
                    声望商人
                    <span>{{ reputation.Guides[0].npcName }}</span>
                </div>
                <div class="map-content">
                    <reputation-map ref="map" :name="reputation.szMapNames[0]" :list="reputation.points" />
                </div>
            </div>
        </div>
        <div class="reputation-reward-wrapper">
            <div class="title">声望奖励</div>
            <div class="reward-content">
                <div class="reward-desc-list">
                    <div
                        class="item"
                        :class="stage === index && 'active'"
                        v-for="(item, index) in reputation.gainList"
                        :key="index"
                        @click="stage = index"
                    >
                        <div class="from-to">{{ item.from }}<i class="el-icon-caret-right"></i>{{ item.to }}</div>
                        <div class="desc">
                            <div class="desc-title">提升方式</div>
                            <div class="desc-content">{{ item.desc }}</div>
                        </div>
                    </div>
                </div>
                <div v-if="reputation.gainList" class="stage-reward-list">
                    <div class="stage-title">
                        <span
                            >阶段奖励（{{
                                stage === -1 ? reputation.gainList[0].from : reputation.gainList[stage].to
                            }}）</span
                        >
                        <div class="page-list" v-if="stageList.length && stageList[0].length">
                            <div
                                class="page-item"
                                :class="page === currentPage && 'active'"
                                v-for="page in pageLen"
                                :key="page"
                                @click="currentPage = page"
                            >
                                {{ page }}
                            </div>
                        </div>
                    </div>
                    <template v-if="stageList.length && stageList[0].length">
                        <div class="list">
                            <item-icon
                                v-for="reward in stageList[currentPage - 1]"
                                :key="reward"
                                :item_id="reward"
                                :onlyIcon="true"
                                :size="36"
                                class="u-item-icon"
                            ></item-icon>
                        </div>
                    </template>
                    <div v-else class="no-data">无</div>
                </div>
            </div>
        </div>
        <div class="m-comment">
            <el-divider content-position="left">讨论</el-divider>
            <Comment :id="id" category="reputation" />
        </div>
    </div>
</template>

<script>
import reputationMap from "@/components/reputation/ReputationMap.vue";
import ItemIcon from "@/components/common/item_icon.vue";
import paths from "@/assets/data/reputation_exchange_path.json";
import levelList from "@/assets/data/reputation_level.json";

import { __imgPath } from "@jx3box/jx3box-common/data/jx3box.json";

import { postStat } from "@jx3box/jx3box-common/js/stat.js";
import { wiki } from "@jx3box/jx3box-common/js/wiki.js";

import { publishLink, ts2str } from "@jx3box/jx3box-common/js/utils";

import { getInfo } from "@/service/reputation";
import Comment from "@jx3box/jx3box-comment-ui/src/Comment.vue";

export default {
    name: "reputationSingle",
    components: {
        reputationMap,
        ItemIcon,
        Comment,
    },
    data() {
        return {
            wiki_post: {
                source: {},
                post: null,
            },
            compatible: false,
            is_empty: true,

            activeTab: 0,
            stage: -1,
            currentPage: 1,
            pageLen: 0,
            stageList: [],

            reputation: {
                dwForceID: -1,
                szName: "",
                szDesc: "",
            },
            loading: false,
        };
    },
    watch: {
        id() {
            this.getData();
            this.loadData();
        },
        post_id: {
            handler() {
                this.loadRevision();
            },
        },
        stage: {
            immediate: true,
            handler(stage) {
                this.currentPage = 1;
                this.stageList = [];
                const reputation = this.reputation;
                const gainList = reputation.gainList;
                if (gainList && gainList.length) {
                    const id = gainList[stage].toID;
                    const stageList = reputation.RewardItems[id] || [];
                    const sLen = stageList.length;
                    if (sLen > 48) {
                        const len = Math.ceil(sLen / 48);
                        this.pageLen = len;
                        for (let i = 0; i < len; i++) {
                            this.stageList.push(stageList.slice(48 * i, 48 * (i + 1)));
                        }
                    } else {
                        this.stageList = [stageList];
                    }
                }
            },
        },
    },
    methods: {
        goBack() {
            this.$router.push({ path: "/" });
        },
        getPath(name) {
            return paths.find((item) => item.reputations.includes(name))
                ? paths.find((item) => item.reputations.includes(name)).path
                : "";
        },
        getIcon(iconPath, type = "icon") {
            const iconName = iconPath ? iconPath.split("\\")[iconPath.split("\\").length - 1].split(".")[0] : "";
            const path = "reputation/reputation/std/" + type + "/";
            if (iconName) {
                return __imgPath + path + iconName.toLowerCase() + ".png";
            } else {
                return "";
            }
        },
        getLevelDesc(level) {
            return levelList.find((item) => item.level === Number(level))
                ? levelList.find((item) => item.level === Number(level)).name
                : "未知";
        },
        getData() {
            this.loading = true;
            getInfo({
                id: this.id,
                client: this.client,
            })
                .then((res) => {
                    const data = res.data;
                    data.szDesc = data.szDesc.replace(/\\n/g, "<br>");
                    data.gainList = data.GainDesc
                        ? data.GainDesc.map((item) => {
                              return {
                                  fromID: Number(item.from),
                                  toID: Number(item.to),
                                  from: this.getLevelDesc(item.from),
                                  to: this.getLevelDesc(item.to),
                                  desc: item.desc,
                              };
                          })
                        : [];
                    data.rewards = data.RewardItems
                        ? Object.keys(data.RewardItems).map((item) => {
                              return {
                                  level: `【${this.getLevelDesc(item)}】声望奖励：`,
                                  list: data.RewardItems[item].map((reward) => {
                                      return {
                                          id: reward,
                                          amount: 1,
                                      };
                                  }),
                              };
                          })
                        : [];
                    data.points = [
                        {
                            mapId: data.szMapIDs,
                            guides: data.Guides && data.Guides.length && data.Guides[0],
                        },
                    ];
                    // console.log(data)
                    this.reputation = data;
                })
                .finally(() => {
                    this.loading = false;
                })
                .catch(() => {
                    this.loading = false;
                });
        },
        handleTabClick(tab, event) {
            if (tab.name == "map") {
                setTimeout(() => {
                    this.$refs.map && this.$refs.map.updateSize();
                }, 100);
            }
        },
        //百科相关
        loadData: function () {
            // 获取最新攻略
            if (this.id) {
                wiki.mix({ type: "reputation", id: this.id, client: this.client }, { supply: 1 }).then((res) => {
                    const { post, source, compatible, isEmpty, users } = res;
                    this.wiki_post = {
                        post: post,
                        source: source,
                        users,
                    };
                    this.is_empty = isEmpty;
                    this.compatible = compatible;
                });
            }
            this.triggerStat();
        },
        loadRevision: function () {
            // 获取指定攻略
            wiki.getById(this.post_id, { type: "reputation" }).then((res) => {
                this.wiki_post = res?.data?.data;
            });
            this.triggerStat();
        },
        publish_url: publishLink,
        triggerStat: function () {
            if (this.client == "origin") {
                postStat("origin_reputation", this.id);
            } else {
                postStat("reputation", this.id);
            }
        },
    },
    mounted() {
        this.getData();
        if (this.post_id) {
            this.loadRevision();
        } else {
            this.loadData();
        }
    },
    computed: {
        id_str: function () {
            return String(this.id);
        },
        id: function () {
            return parseInt(this.$route.params.reputation_id);
        },
        showMap: function () {
            return this.reputation.szMapIDs;
        },
        showDialog: function () {
            return this.reputation.servant;
        },
        showReward: function () {
            return this.reputation.RewardItems;
        },
        showPath() {
            return this.reputation.gainList && this.reputation.gainList.length;
        },
        client() {
            return this.$store.state.client;
        },

        //wiki相关
        post_id: function () {
            return this.$route.params.post_id;
        },
        isRevision: function () {
            return !!this.$route.params.post_id;
        },
        author_id: function () {
            return ~~this.wiki_post?.post?.user_id;
        },
        user_name: function () {
            return this.wiki_post?.post?.user_nickname;
        },
        updated_at: function () {
            return ts2str(this.wiki_post?.post?.updated);
        },
        fav_title: function () {
            return this.wiki_post?.source?.Name;
        },
        authors: function () {
            if (!this.isRevision) {
                return (
                    this.wiki_post?.users
                        ?.filter((user) => user.id)
                        ?.map((user) => {
                            return {
                                user_id: user.id,
                                user_avatar: user.avatar,
                                display_name: user.nickname,
                            };
                        }) || []
                );
            }
            return [];
        },
    },
};
</script>

<style lang="less">
@import "~@/assets/css/reputation/single.less";
</style>
